import React, { useReducer, useEffect, useState } from "react";
import '../styles/App.css';
import Header from "./Header";
import Movie from "./Movie";
import Search from "./Search";
import Nominate from './Nominate';
import RemoveNominated from './RemoveNominated';
import Popup from "./Popup";



const initialState = {
  loading: true,
  movies: [],
  errorMessage: null,
};

const reducer = (state, action) => {
  switch (action.type) {
    case "SEARCH_MOVIES_REQUEST":
      return {
        ...state,
        loading: true,
        errorMessage: null,
      };
    case "SEARCH_MOVIES_SUCCESS":
      return {
        ...state,
        loading: false,
        movies: action.payload,
      };
    case "SEARCH_MOVIES_FAILURE":
      return {
        ...state,
        loading: false,
        errorMessage: action.error,
      };
    default:
      return state;
  }
};

const App = () => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [nominated, setNominated] = useState([]);
  const [disable, setDisabled] = useState([]);
  
  useEffect(() => {
    const movieNominated = JSON.parse(
      localStorage.getItem('react-movie-app-nominated')
    );
    if (movieNominated) {
      setNominated(movieNominated);
    }
  }, [])
  const saveToLocalStorage = (items) => {
    localStorage.setItem('react-movie-app-nominated', JSON.stringify(items))
  }

  const search = (searchValue) => {
    dispatch({
      type: "SEARCH_MOVIES_REQUEST",
    });

    fetch(`https://www.omdbapi.com/?s=${searchValue}&apikey=8ceee85f`)
      .then((response) => response.json())
      .then((jsonResponse) => {
        if (jsonResponse.Response === "True") {
          dispatch({
            type: "SEARCH_MOVIES_SUCCESS",
            payload: jsonResponse.Search,
          });
        } else {
          dispatch({
            type: "SEARCH_MOVIES_FAILURE",
            error: jsonResponse.Error,
          });
        }
      });
  };
  const addDisableKey = (value) => {
    return nominated.some(item => value.Title === item.Title);
  }
 
 const handleDisabled = (movie) => {
   const array = {
     id: 1,
     movie: movie.Title,
     condition: false,
   };
   const specific = [...disable, array];
   setDisabled(specific);
 };
  const addNominatedMovie = (movie) => {
    const condition = addDisableKey(movie)
    const newNominatedList = [...nominated, movie];
    if (condition === true) {
      handleDisabled(movie);
    } else if(condition ===false){
      setNominated(newNominatedList);
      saveToLocalStorage(newNominatedList);
    }
    
  }

  const RemoveNominatedMovie = (movie) =>{
    const newNominatedList = nominated.filter(
      (nominatedMovie) => nominatedMovie.imdbID !== movie.imdbID
    );
    setNominated(newNominatedList);
    saveToLocalStorage(newNominatedList);
  }
  const { movies, errorMessage, loading } = state;
  console.log(nominated.length)

    return (
    <div className="App">
      <Header text="Nominate Your Favourite Movies" />
      <Search search={search} />
      <p className="App-intro">Search for the movie you wish to nominate</p>
      <div className="movies">
        {loading && !errorMessage ? (
          <span>loading... </span>
        ) : errorMessage ? (
          <div className="errorMessage">{errorMessage}</div>
        ) : ( nominated.length >= 5 ? <Popup /> :
          movies.map((movie, index) => (
            <Movie
              key={`${index}-${movie.Title}`}
              handleDisable={index}
              movie={movie}
              NominateComponent={Nominate}
              handleNominatedClick={addNominatedMovie}
            />
        ))
        )}
      </div>
      <Header text="Nominated" />
      <div className="movies nominated">
       {
          nominated.map((movie, index) => (
            <Movie
              key={`${index}-${movie.Title}`}
              movie={movie}
              handleNominatedClick={RemoveNominatedMovie}
              NominateComponent={RemoveNominated}
            />
          ))
        }
      </div>
    </div>
  );
};

export default App;
